body {
  font-weight: 400;
  font-family: "Roboto", Arial, sans-serif;
  line-height: 1.6;
  color: #333333;
  text-rendering: optimizelegibility;
  padding: 50px 50px 50px 50px;
}

.page-header {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  border-bottom: 1px solid #eee;
}

.page-header__history {
  font-size: 12px;
}

.main-container {
  display: flex;
  flex-direction: column;
}

.main-container__title {
  font-size: 35px;
  text-align: left;
}

.main-container__qustion {
  font-size: 35px;
  text-align: left;
}

.qustion-form {
  display: flex;
  flex-wrap: wrap;
  position: relative;
}

.qustion-form__block {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 355px;
  margin-right: 40px;
  margin-bottom: 50px;
  box-sizing: border-box;
  padding: 25px 25px 11px 25px;
  border: 1px solid #eee;
  border-top: 2px solid #f00;

  border-radius: 4px;
}

.qustion-form__button {
  position: absolute;
  bottom: 0;
  right: 47%;
  color: #fff;
  padding: 5px 5px 5px 5px;
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  background-color: #3949a0;
}

.qustion-form__label {
  position: relative;
  font-size: 28px;
  margin-bottom: 20px;
}

.qustion-form__label::after {
  position: absolute;
  left: 0;
  top: 60px;
  content: "";
  height: 1pxs;
  width: 50px;
  border-bottom: 1px solid black;
}

.qustion-form__input {
  position: relative;
  margin-right: 10px;
  border: none;
  box-sizing: border-box;
  height: 20px;
  width: 50px;
  border: 1px solid black;
}

.qustion-form__input-container {
  display: flex;
  order: 2;
  align-self: center;
  align-items: center;
}

.qustion-form__input-container p {
  font-size: 10px;
}

.qustion-form__description {
  font-size: 18px;
  margin-bottom: 40px;
  padding-bottom: 20px;
  border-bottom: 1px solid #eee;
  box-sizing: border-box;
}
